import './Lists.css';
import { useState, useLayoutEffect, useRef, useContext } from 'react';
import $ from 'jquery';
import ClearIcon from '@mui/icons-material/Clear';
import Cards from './Cards';
import Titles from './Titles';
import StoreApi from '../../Utils/StoreApi';
import { ContextTypes } from '../../@types/ContextTypes';
import { Draggable, Droppable } from 'react-beautiful-dnd';
import InputCards from './InputCards';

export default function List({
	list,
	listId,
	cards,
	index,
}: {
	list: { id: string; title: string };
	listId: string;
	cards: { id: string; text: string }[];
	index: number;
}) {
	const [open, setOpen] = useState(false);
	const [text, setText] = useState('');
	const { addMoreCard, deleteList } = useContext(StoreApi) as ContextTypes;

	const inputRef = useRef<HTMLTextAreaElement>(null);

	const mouseEnter = (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
		const target = e.currentTarget.firstElementChild /*childNodes[0]*/ /*firstChild*/ as HTMLDivElement;
		if (target) {
			target.style.display = 'block';
		}
	};

	const mouseLeave = (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
		const target = e.currentTarget.firstElementChild /*childNodes[0]*/ /*firstChild*/ as HTMLDivElement;
		if (target) {
			target.style.display = 'none';
		}
	};

	/* Keep Textarea Height Auto While Typing */

	const [height, setHeight] = useState(0);
	const div = useRef<HTMLParagraphElement>(null);

	useLayoutEffect(() => {
		if (div.current && open === true) {
			setHeight(div.current.scrollHeight);
		}
	}, [open, text]);

	/* On Clicking Buttons Events */

	const focusInput = (e: React.FocusEvent<HTMLTextAreaElement, Element>) => {
		const slider = document.querySelector('.Wrapper');

		/*Scroll inputCard Into Left View*/

		const inputLeft = e.target /*e.currentTarget*/
			.getBoundingClientRect().left;

		if (slider && slider.scrollLeft <= 24) {
			/*slider.scrollBy*/ slider.scrollTo({
				left: -slider.scrollLeft /*0 With slider.scrollTo (Only)*/,
				behavior: 'smooth',
			});
		} else if (slider && inputLeft < 20) {
			slider.scrollTo({
				left: slider.scrollLeft + inputLeft - 20,
				behavior: 'smooth',
			});
		}

		/*Scroll inputCard Into Right View*/

		if (slider && slider.clientWidth - inputLeft < 268) {
			slider.scrollTo({
				left: slider.scrollLeft + inputLeft - slider.clientWidth + 268,
				behavior: 'smooth',
			});
		}
	};

	const btn1 = () => {
		if (inputRef.current && text.trim().length === 0) {
			inputRef.current.focus();
			setText('');
			setOpen(open);
		} else {
			addMoreCard(text.trim(), listId);
			setText('');
			setOpen(!open);
		}
	};

	const btn2 = () => {
		setOpen(!open);
		setText('');
	};

	/* On Key Down Events */

	const keyDown = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
		if (e.key === 'Enter') {
			e.preventDefault();
			$(document).trigger('click');
			btn1();
		} else if (e.key === 'Escape') {
			$(document).trigger('click');
			setText(text.trim());
			if (text.trim().length === 0) {
				setText('');
			}
		}
	};

	/* Clicking Outside The Div Closing It */

	$(document).on('click', (e) => {
		if (open === true) {
			if ($(e.target).closest('#sideBarBtn-blur').length === 0 && $(e.target).closest('#sideBar-blur').length === 0) {
				if ($(e.target).closest('#bottomBar-blur').length === 0 && $(e.target).closest('#bottomBar-blur2').length === 0) {
					if ($(e.target).closest('#List-blur').length === 0) {
						if ($(e.target).closest('#newList-blur').length === 0 && $(e.target).closest('#newList-blur1').length === 0) {
							setOpen(!open);
							setText('');
						}
					}
				}
			}
		}
	});

	return (
		<Draggable
			draggableId={list.id}
			index={index}>
			{(provided) => (
				<section
					ref={provided.innerRef}
					{...provided.draggableProps}
					className='Lists'>
					<div
						onMouseEnter={mouseEnter}
						onMouseLeave={mouseLeave}
						className='List-wrapper'
						id='List-blur'>
						<div
							onClick={() => deleteList(listId)}
							className='del-list-btn-wrapper'>
							<span className='del-list-btn'></span>
						</div>
						<div
							className='Titles'
							{...provided.dragHandleProps}>
							<Titles
								title={list.title}
								listId={list.id}
							/>
						</div>
						<Droppable
							droppableId={list.id}
							key={index}
							type='card'>
							{(provided) => (
								<>
									<div
										className='List'
										ref={provided.innerRef}
										{...provided.droppableProps}>
										{cards.map((card: { id: string; text: string }, index: number) => (
											<Cards
												key={card.id}
												card={card}
												text={card.text}
												listId={listId}
												cardId={card.id}
												index={index}
											/>
										))}
										{provided.placeholder}
										<div>
											{open && (
												<div>
													<p
														className='inputCard-hide'
														ref={div}>
														{text}
													</p>
													<textarea
														style={{ height }}
														autoFocus
														ref={inputRef}
														onKeyDown={keyDown}
														onFocus={focusInput}
														onChange={(e) => setText(e.target.value)}
														placeholder='Enter a title for this card...'
														className='inputCard-input'
														value={text}
														name='text'
														autoComplete='off'
													/>
													<div>
														<button
															title='Add card'
															className='inputCard-btn'
															onClick={btn1}>
															Add card
														</button>
														<ClearIcon
															className='inputCard-icon'
															onClick={btn2}
														/>
													</div>
												</div>
											)}
										</div>
									</div>
									{setOpen && (
										<InputCards
											open={open}
											setOpen={setOpen}
										/>
									)}
								</>
							)}
						</Droppable>
					</div>
				</section>
			)}
		</Draggable>
	);
}
