import './index.css';
import { useState, useEffect, useCallback } from 'react';
import Wrapper from './Components/Wrapper';
import Bars from './Components/Bars/Bars';
import Store from './Utils/Store';
import StoreApi from './Utils/StoreApi';
import { AppTypes } from './@types/ContextTypes';
import Colors from './Utils/Colors';
import LinearGradients from './Utils/LinearGradients';
import ImageApi from './Utils/ImageApi';

export default function App() {
	const [data, setData] = useState(Store);
	const [bgColor, setBgColor] = useState('');
	const [btnArrayColor, setBtnArrayColor] = useState('');
	const [btnArrayLinear, setBtnArrayLinear] = useState('');
	const [btnArrayPhoto, setBtnArrayPhoto] = useState('');
	const [btnColors, setBtnColors] = useState('');
	const [btnImages, setBtnImages] = useState('');
	const [btnNextColor, setBtnNextColor] = useState('');
	const [btnColor, setBtnColor] = useState('');
	const [oneSecondBtnColor, setOneSecondBtnColor] = useState('');
	const [thirtySecondBtnColor, setThirtySecondBtnColor] = useState('');
	const [oneMinuteBtnColor, setOneMinuteBtnColor] = useState('');
	const [fiveMinutesBtnColor, setFiveMinutesBtnColor] = useState('');
	const [tenMinutesBtnColor, setTenMinutesBtnColor] = useState('');
	const [bgImage, setBgImage] = useState('');
	const [images, setImages] = useState([]);

	const updateBar = (barTitle: string, barId: string) => {
		const bar = data.Bars[barId as keyof typeof data.Bars];
		bar.barTitle = barTitle;

		const newState = {
			...data,
			Bars: {
				...data.Bars,
				[barId]: bar,
			},
		};
		setData(newState);
	};

	const getImageList = async () => {
		setImages(await ImageApi());
	};

	useEffect(() => {
		getImageList();
	}, []);

	const RandomBgArrayColor = useCallback(() => {
		const arrColor = Math.floor(Math.random() * Colors.length);
		const colorValue = Colors[arrColor];
		setBtnArrayColor(colorValue);
	}, []);

	const RandomBgArrayLinear = useCallback(() => {
		const arrColor = Math.floor(Math.random() * Colors.length);
		const colorValue = Colors[arrColor];
		setBtnArrayLinear(colorValue);
	}, []);

	const RandomBgArrayPhoto = useCallback(() => {
		const arrColor = Math.floor(Math.random() * Colors.length);
		const colorValue = Colors[arrColor];
		setBtnArrayPhoto(colorValue);
	}, []);

	const RandomBgColors = useCallback(() => {
		const arrColor = Math.floor(Math.random() * Colors.length);
		const colorValue = Colors[arrColor];
		setBtnColors(colorValue);
	}, []);

	const RandomBgImages = useCallback(() => {
		const arrColor = Math.floor(Math.random() * Colors.length);
		const colorValue = Colors[arrColor];
		setBtnImages(colorValue);
	}, []);

	const RandomBgNextColor = useCallback(() => {
		const arrColor = Math.floor(Math.random() * Colors.length);
		const colorValue = Colors[arrColor];
		setBtnNextColor(colorValue);
	}, []);

	const RandomBgColor = useCallback(() => {
		const arrColor = Math.floor(Math.random() * Colors.length);
		const colorValue = Colors[arrColor];
		setBtnColor(colorValue);
	}, []);

	const RandomOneSecondBgColor = useCallback(() => {
		const arrColor = Math.floor(Math.random() * Colors.length);
		const colorValue = Colors[arrColor];
		setOneSecondBtnColor(colorValue);
	}, []);

	const RandomThirtySecondBgColor = useCallback(() => {
		const arrColor = Math.floor(Math.random() * Colors.length);
		const colorValue = Colors[arrColor];
		setThirtySecondBtnColor(colorValue);
	}, []);

	const RandomOneMinuteBgColor = useCallback(() => {
		const arrColor = Math.floor(Math.random() * Colors.length);
		const colorValue = Colors[arrColor];
		setOneMinuteBtnColor(colorValue);
	}, []);

	const RandomFiveMinutesBgColor = useCallback(() => {
		const arrColor = Math.floor(Math.random() * Colors.length);
		const colorValue = Colors[arrColor];
		setFiveMinutesBtnColor(colorValue);
	}, []);

	const RandomTenMinutesBgColor = useCallback(() => {
		const arrColor = Math.floor(Math.random() * Colors.length);
		const colorValue = Colors[arrColor];
		setTenMinutesBtnColor(colorValue);
	}, []);

	const RandomBg = useCallback(() => {
		const arrColor = Math.floor(Math.random() * Colors.length);
		const colorValue = Colors[arrColor];

		const arrLinearGradient = Math.floor(Math.random() * LinearGradients.length);
		const linearGradientValue = LinearGradients[arrLinearGradient];

		const allImages = images.map((image) => {
			return `url(${image['full']})`;
		});

		const arrAllImages = Math.floor(Math.random() * allImages.length);
		const imagesValue = allImages[arrAllImages];

		let storeValues = [colorValue, linearGradientValue, imagesValue];

		if (localStorage.hasOwnProperty('storedBgColor')) {
			const filteredItems = storeValues.filter((item) => item !== colorValue);
			storeValues = filteredItems;
		}
		if (localStorage.hasOwnProperty('storedBgLinear')) {
			const filteredItems = storeValues.filter((item) => item !== linearGradientValue);
			storeValues = filteredItems;
		}
		if (localStorage.hasOwnProperty('storedBgPhoto')) {
			const filteredItems = storeValues.filter((item) => item !== imagesValue);
			storeValues = filteredItems;
		}

		const arrValue = Math.floor(Math.random() * storeValues.length);

		const storedValue = storeValues[arrValue];

		if (storedValue === colorValue) {
			setBgColor(storedValue);
			setBgImage('');
		} else {
			setBgImage(storedValue);
			setBgColor('');
		}
	}, [images]);

	const value = {
		setBgImage,
		setBgColor,
		setBtnColors,
		setBtnImages,
		setBtnNextColor,
		setBtnColor,
		setOneSecondBtnColor,
		setThirtySecondBtnColor,
		setOneMinuteBtnColor,
		setFiveMinutesBtnColor,
		setTenMinutesBtnColor,
		updateBar,
		btnArrayColor,
		btnArrayLinear,
		btnArrayPhoto,
		btnColors,
		btnImages,
		btnNextColor,
		btnColor,
		oneSecondBtnColor,
		thirtySecondBtnColor,
		oneMinuteBtnColor,
		fiveMinutesBtnColor,
		tenMinutesBtnColor,
		RandomBg,
		RandomBgArrayColor,
		RandomBgArrayLinear,
		RandomBgArrayPhoto,
		RandomBgColors,
		RandomBgImages,
		RandomBgNextColor,
		RandomBgColor,
		RandomOneSecondBgColor,
		RandomThirtySecondBgColor,
		RandomOneMinuteBgColor,
		RandomFiveMinutesBgColor,
		RandomTenMinutesBgColor,
		images,
	};

	return (
		<StoreApi.Provider value={value as AppTypes}>
			<main
				className='Index'
				style={{ backgroundColor: bgColor, backgroundImage: bgImage, backgroundSize: 'cover' }}>
				{data.barIds.map((barId) => {
					const bar = data.Bars[barId as keyof typeof data.Bars];
					return (
						<Bars
							key={bar.id}
							barId={bar.id}
							barTitle={bar.barTitle}
						/>
					);
				})}
				<section className='App'>
					<Wrapper />
				</section>
			</main>
		</StoreApi.Provider>
	);
}
