import './ToggleBtn.css';
import { useState, useEffect, useCallback } from 'react';
import $ from 'jquery';

export default function ToggleBtn() {
	const [isDark, setIsDark] = useState<'dark' | 'light' /*| MediaQueryList*/ | boolean>();

	/* Look for a local storage value. */

	const calculateSettingAsThemeString = ({ localStorageTheme, darkThemeMq }: { localStorageTheme: string; darkThemeMq: MediaQueryList }) => {
		if (localStorageTheme !== null) {
			return localStorageTheme;
		}

		/* Fall back to The system setting. */

		if (darkThemeMq.matches) {
			return 'dark';
		}

		/* Fall back to The light mode. */

		return 'light';
	};

	/* Fall Back To The System Setting And The Page Default */

	const mqListener = useCallback(
		(e: MediaQueryListEvent) => {
			setIsDark(e.matches);
		},
		[setIsDark],
	);

	/* Utility Function To Update The Button Text And Aria-Label. */

	const updateButton = ({ buttonEl, isDark }: { buttonEl: HTMLButtonElement; isDark: boolean }) => {
		if (!localStorage.hasOwnProperty('theme')) {
			const newCta = 'System Default';

			/* Use An Aria-Label If You Are Omitting Text On The Button */

			buttonEl.setAttribute('aria-label', newCta);
			buttonEl.innerText = newCta;

			if (isDark === true) {
				const html = `<svg xmlns="http://www.w3.org/2000/svg" width="2em" height="2em" viewBox="0 0 24 24"><path fill="black" d="M11 21q-3.35 0-5.675-2.325T3 13q0-3.35 2.325-5.675T11 5q.125 0 .25.012t.25.013q-.725.8-1.112 1.825T10 9q0 2.5 1.75 4.25T16 15q.775 0 1.513-.187t1.387-.563q-.45 2.95-2.7 4.85T11 21m2.8-10L17 2h2l3.2 9h-1.9l-.7-2h-3.2l-.7 2zm3.05-3.35h2.3L18 4z"/></svg>`;
				buttonEl.insertAdjacentHTML('beforeend', html);
			} else if (isDark === false) {
				const html = `<svg xmlns="http://www.w3.org/2000/svg" width="2em" height="2em" viewBox="0 0 24 24"><path fill="currentColor" d="M11 21q-3.35 0-5.675-2.325T3 13q0-3.35 2.325-5.675T11 5q.125 0 .25.012t.25.013q-.725.8-1.112 1.825T10 9q0 2.5 1.75 4.25T16 15q.775 0 1.513-.187t1.387-.563q-.45 2.95-2.7 4.85T11 21m2.8-10L17 2h2l3.2 9h-1.9l-.7-2h-3.2l-.7 2zm3.05-3.35h2.3L18 4z"/></svg>`;
				buttonEl.insertAdjacentHTML('beforeend', html);
			}
		} else {
			const newCta = isDark ? 'Dark Mode' : 'Light Mode';

			/* Use An Aria-Label If You Are Omitting Text On The Button */

			buttonEl.setAttribute('aria-label', newCta);
			buttonEl.innerText = newCta;

			if (isDark === true) {
				const html = `<svg xmlns="http://www.w3.org/2000/svg" width="2em" height="2em" viewBox="0 0 24 24"><path fill="black" d="M12 21q-3.75 0-6.375-2.625T3 12q0-3.75 2.625-6.375T12 3q.35 0 .688.025t.662.075q-1.025.725-1.638 1.888T11.1 7.5q0 2.25 1.575 3.825T16.5 12.9q1.375 0 2.525-.613T20.9 10.65q.05.325.075.662T21 12q0 3.75-2.625 6.375T12 21"/></svg>`;
				buttonEl.insertAdjacentHTML('beforeend', html);
			} else if (isDark === false) {
				const html = `<svg xmlns="http://www.w3.org/2000/svg" width="2em" height="2em" viewBox="0 0 24 24"><circle cx="12" cy="32" r="6" fill="currentColor"><animate fill="freeze" attributeName="cy" dur="0.6s" values="32;12"/></circle><g fill="none" stroke="currentColor" strokeDasharray="2" strokeDashoffset="2" strokeLinecap="round" strokeWidth="2"><path d="M0 0"><animate fill="freeze" attributeName="d" begin="0.7s" dur="0.2s" values="M12 19v1M19 12h1M12 5v-1M5 12h-1;M12 21v1M21 12h1M12 3v-1M3 12h-1"/><animate fill="freeze" attributeName="stroke-dashoffset" begin="0.7s" dur="0.2s" values="2;0"/></path><path d="M0 0"><animate fill="freeze" attributeName="d" begin="0.9s" dur="0.2s" values="M17 17l0.5 0.5M17 7l0.5 -0.5M7 7l-0.5 -0.5M7 17l-0.5 0.5;M18.5 18.5l0.5 0.5M18.5 5.5l0.5 -0.5M5.5 5.5l-0.5 -0.5M5.5 18.5l-0.5 0.5"/><animate fill="freeze" attributeName="stroke-dashoffset" begin="0.9s" dur="0.2s" values="2;0"/></path><animateTransform attributeName="transform" dur="30s" repeatCount="indefinite" type="rotate" values="0 12 12;360 12 12"/></g></svg>`;
				buttonEl.insertAdjacentHTML('beforeend', html);
			}
		}
	};

	/* Utility Function To Update The theme Setting On The Html Tag */

	const updateThemeOnHtmlEl = ({ theme }: { theme: string }) => {
		const target = document.querySelector('html')!; /* as HTMLHtmlElement*/
		target.setAttribute('data-theme', theme);
	};

	/* The Click Function */

	/*const btn1 = () => {
		/* Grab What We Need From The DOM And System Settings On Page Load */

	/*const button = document.querySelector('[data-theme-toggle]');
		const localStorageTheme = localStorage.getItem('theme');

		/* Utility Function To Calculate The Current Theme Setting. */

	/*const darkThemeMq = window.matchMedia('(prefers-color-scheme: dark)');

		/* Work Out TThe Current Site Settings */

	/*const currentThemeSetting = calculateSettingAsThemeString({ localStorageTheme, darkThemeMq });

		const newTheme = currentThemeSetting === 'dark' ? 'light' : 'dark';

		localStorage.setItem('theme', newTheme);
		updateButton({ buttonEl: button, isDark: newTheme === 'dark' });
		updateThemeOnHtmlEl({ theme: newTheme });
		setIsDark(newTheme);
	};*/

	/* The Right Click Function */

	/*const contextMenu = (e, darkThemeMq) => {
		e.preventDefault();

		const button = document.querySelector('[data-theme-toggle]');

		localStorage.removeItem('theme');
		updateButton({ buttonEl: button, isDark: darkThemeMq });

		setIsDark(darkThemeMq);
	};*/

	const onStorageUpdate = useCallback((e: StorageEvent) => {
		const { newValue } = e;

		const button = document.querySelector('[data-theme-toggle]') as HTMLButtonElement;

		const darkThemeMq = window.matchMedia('(prefers-color-scheme: dark)');

		if (!localStorage.hasOwnProperty('theme')) {
			updateButton({ buttonEl: button, isDark: darkThemeMq.matches });

			setIsDark(darkThemeMq.matches);
		} else {
			updateButton({ buttonEl: button, isDark: darkThemeMq.matches });
			updateThemeOnHtmlEl({ theme: newValue as 'dark' | 'light' });

			setIsDark(newValue as 'dark' | 'light');
		}
	}, []);

	useEffect(() => {
		/* Grab What We Need From The DOM And System Settings On Page Load */

		const button = document.querySelector('[data-theme-toggle]') as HTMLButtonElement;
		const localStorageTheme = localStorage.getItem('theme')!; /* as string*/

		/* Utility Function To Calculate The Current Theme Setting. */

		const darkThemeMq = window.matchMedia('(prefers-color-scheme: dark)');

		/* Work Out TThe Current Site Settings */

		const currentThemeSetting = calculateSettingAsThemeString({ localStorageTheme, darkThemeMq });

		/* Update The Theme Setting And Button Text According To Current Settings */

		updateButton({ buttonEl: button, isDark: currentThemeSetting === 'dark' });
		updateThemeOnHtmlEl({ theme: currentThemeSetting });

		/* The Left Click Function */

		const btn1 = () => {
			const newTheme = currentThemeSetting === 'dark' ? 'light' : 'dark';

			localStorage.setItem('theme', newTheme);
			updateButton({ buttonEl: button, isDark: newTheme === 'dark' });
			updateThemeOnHtmlEl({ theme: newTheme });
			setIsDark(newTheme);
		};

		/* The Right Click Function */

		const contextMenu = (e: Event) => {
			e.preventDefault();

			localStorage.removeItem('theme');
			updateButton({ buttonEl: button, isDark: darkThemeMq.matches });

			setIsDark(darkThemeMq.matches);
		};

		/* The Reset Button Left Click Function */

		$('.reset').on('click', () => {
			localStorage.removeItem('theme');
			updateButton({ buttonEl: button, isDark: darkThemeMq.matches });

			setIsDark(darkThemeMq.matches);
		});

		/* Add Event Listeners */

		if (button) {
			button.addEventListener('click', btn1);
			button.addEventListener('contextmenu', contextMenu);
			darkThemeMq.addEventListener('change', mqListener);
			window.addEventListener('storage', onStorageUpdate);

			/* Remove Event Listeners */

			return () => {
				button.removeEventListener('click', btn1);
				button.removeEventListener('contextmenu', contextMenu);
				darkThemeMq.removeEventListener('change', mqListener);
				window.removeEventListener('storage', onStorageUpdate);
			};
		}
	}, [onStorageUpdate, mqListener, isDark]);

	return (
		<button
			title='System Default:
			Right Click,
			Touch Devices: Hold The Button For A Second.'
			className='sideBar-btn'
			data-theme-toggle
			aria-label=''></button>
	);
}
