import './InputCards.css';
import AddIcon from '@mui/icons-material/Add';

export default function InputCards({ open, setOpen }: { open: boolean; setOpen: React.Dispatch<React.SetStateAction<boolean>> }) {
	const btn1 = (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
		setOpen(!open);

		/*Prevent React Beautiful DND Default Scroll*/
		const target = e.target as HTMLDivElement;
		target.scrollIntoView();

		const slider = document.querySelector('.Wrapper');

		if (slider) {
			slider.scrollTo({ top: 0 });
		} /*

		/*Scroll inputCard Into Left View*/ /*,

		/*const inputLeft = e.target /*e.currentTarget*/
		/*.getBoundingClientRect().left;

		if (slider.scrollLeft <= 24) {
			/*slider.scrollBy*/
		/*left: -slider.scrollLeft /*0 With slider.scrollTo (Only)*/
		/*behavior: 'smooth',
			});
		} else if (inputLeft < 20) {
			slider.scrollTo({
				left: slider.scrollLeft + inputLeft - 20,
				behavior: 'smooth',
			});
		}

		/*Scroll inputCard Into Right View*/

		/*if (slider.clientWidth - inputLeft < 268) {
			slider.scrollTo({
				left: slider.scrollLeft + inputLeft - slider.clientWidth + 268,
				behavior: 'smooth',
			});
		}*/
	};

	return (
		<div
			style={{ display: open ? 'none' : 'block' }}
			onClick={btn1}
			className='inputCard-wrap'>
			<div className='inputCard-btn-wrapper'>
				<AddIcon />
				<p>Add a card</p>
			</div>
		</div>
	);
}
