import './Titles.css';
import { useState, useLayoutEffect, useRef, useContext } from 'react';
import $ from 'jquery';
import MoreHoriz from '@mui/icons-material/MoreHoriz';
import StoreApi from '../../Utils/StoreApi';
import { ContextTypes } from '../../@types/ContextTypes';

export default function Title({ title, listId }: { title: string; listId: string }) {
	const [open, setOpen] = useState(false);
	const [newTitle, setNewTitle] = useState(title);
	const { updateListTitle } = useContext(StoreApi) as ContextTypes;

	/* Keep Textarea Height Auto While Typing */

	const [height, setHeight] = useState(0);
	const div = useRef<HTMLParagraphElement>(null);

	useLayoutEffect(() => {
		if (div.current && open === true) {
			setHeight(div.current.scrollHeight);
		}
	}, [open, newTitle]);

	/* On Clicking Buttons Events */

	const btn1 = (e: React.MouseEvent<HTMLParagraphElement, MouseEvent>) => {
		setOpen(!open);

		/*Prevent React Beautiful DND Default Scroll*/
		const target = e.target as HTMLParagraphElement;
		target.scrollIntoView();

		const slider = document.querySelector('.Wrapper');

		/*Scroll Title Into Top View*/
		if (slider) {
			slider.scrollTop = 0;

			/*Scroll Title Into Left View*/

			const titleLeft = target /*e.currentTarget*/
				.getBoundingClientRect().left;

			if (slider.scrollLeft <= 20) {
				/*slider.scrollBy*/ slider.scrollTo({
					left: -slider.scrollLeft /*0 With slider.scrollTo (Only)*/,
					behavior: 'smooth',
				});
			} else if (titleLeft < 16) {
				slider.scrollTo({
					left: slider.scrollLeft + titleLeft - 16,
					behavior: 'smooth',
				});
			}

			/*Scroll Title Into Right View*/

			if (slider.clientWidth - titleLeft < 272) {
				slider.scrollTo({
					left: slider.scrollLeft + titleLeft - slider.clientWidth + 272,
					behavior: 'smooth',
				});
			}
		}
	};

	/* Clicking Outside The Div Closing It */

	const blur = (e: React.FocusEvent<HTMLTextAreaElement, Element>) => {
		if ($(e.target).closest('#sideBarBtn-blur').length === 0 && $(e.target).closest('#sideBar-blur').length === 0) {
			if ($(e.target).closest('#bottomBar-blur').length === 0 && $(e.target).closest('#bottomBar-blur2').length === 0) {
				if ($(e.target).closest('#newList-blur').length === 0 && $(e.target).closest('#newList-blur1').length === 0) {
					setOpen(!open);

					setNewTitle(newTitle.trim());
					newTitle.trim().length === 0 ? setNewTitle(title) : updateListTitle(newTitle.trim(), listId);
				}
			}
		}
	};

	/* On Key Down Events */

	const keyDown = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
		if (e.key === 'Enter') {
			e.preventDefault();
			$(document).trigger('click');
			setOpen(!open);

			setNewTitle(newTitle.trim());
			newTitle.trim().length === 0 ? setNewTitle(title) : updateListTitle(newTitle.trim(), listId);
		} else if (e.key === 'Escape') {
			$(document).trigger('click');
			setOpen(!open);

			setNewTitle(newTitle.trim());
			newTitle.trim().length === 0 ? setNewTitle(title) : updateListTitle(newTitle.trim(), listId);
		}
	};

	return (
		<>
			{open ? (
				<div className='titles-inputWrapper'>
					<p
						className='titles-hide overflow'
						ref={div}>
						{newTitle}
					</p>
					<textarea
						style={{ height }}
						maxLength={512}
						value={newTitle}
						onChange={(e) => setNewTitle(e.target.value)}
						onFocus={(e) => e.target.select()}
						className='titles-input overflow'
						autoFocus
						onKeyDown={keyDown}
						onBlur={blur}
						name='text'
						autoComplete='off'
					/>
				</div>
			) : (
				<p
					onClick={btn1}
					className='titles-wrapper overflow'>
					{title}
				</p>
			)}
			<div className='horiz-wrapper'>
				<MoreHoriz
					fontSize='small'
					className='horiz'
				/>
			</div>
		</>
	);
}
