import './ScrollRight.css';
import { useState, useEffect } from 'react';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';

export default function ScrollRight() {
	const [offsetWidth, setOffsetWidth] = useState(0);
	const [scrollWidth, setScrollWidth] = useState(0);

	const listsNumber = document.querySelectorAll('.List');

	const toggleRight = () => {
		const slider = document.querySelector('.Wrapper');
		const scrollRight = document.querySelector('#angle-right') as HTMLButtonElement;

		//if (slider) {
		const scrolledRight = slider?.scrollWidth! - slider?.scrollLeft! - slider?.clientWidth!;
		if (scrolledRight >= slider?.clientWidth! / 10) {
			scrollRight.style.display = 'block';
		} else if (scrolledRight < slider?.clientWidth! / 10) {
			scrollRight.style.display = 'none';
		}
		//}
	};

	const scrollRight = () => {
		const slider = document.querySelector('.Wrapper');

		//if (slider) {
		/*slider.scrollBy*/ slider?.scrollTo({
			left: slider.scrollLeft + slider.clientWidth /*-slider.clientWidth*/,
			behavior: 'smooth',
		});
		//}
	};

	const resize = () => {
		const slider = document.querySelector('.Wrapper') as HTMLElement;

		/*console.log('width: ', slider.offsetWidth window.innerWidth + 'px');
		console.log('width: ', slider.scrollWidth + 'px');*/
		setOffsetWidth(slider.offsetWidth);
		setScrollWidth(slider.scrollWidth);
	};
	window.onresize = resize;

	/* Add The Event Listeners */

	useEffect(() => {
		const slider = document.querySelector('.Wrapper') as HTMLElement;

		setOffsetWidth(slider?.offsetWidth);
		setScrollWidth(slider?.scrollWidth!);

		if (offsetWidth < scrollWidth) {
			toggleRight();
		} else if (offsetWidth >= scrollWidth) {
			toggleRight();
		}
	}, [offsetWidth, scrollWidth, listsNumber]);

	useEffect(() => {
		const slider = document.querySelector('.Wrapper');

		//if (slider) {
		slider?.addEventListener('scroll', toggleRight);
		return () => {
			slider?.removeEventListener('scroll', toggleRight);
		};
		//}
	}, []);

	return (
		<button
			id='angle-right'
			onClick={scrollRight}>
			<ChevronRightIcon />
		</button>
	);
}
