import './SideBars.css';
import { useContext } from 'react';
import $ from 'jquery';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css';
import PhotosBar from './PhotosBar';
import ColorsBar from './ColorsBar';
import ToggleBtn from './ToggleBtn';
import Buttons from './Buttons';
import StoreApi from '../../Utils/StoreApi';
import { ContextTypes } from '../../@types/ContextTypes';
import Photos from '../../Utils/Photos.webp';
import Colors from '../../Utils/Colors.webp';

export default function SideBars() {
	const { btnImages, btnColors } = useContext(StoreApi) as ContextTypes;

	const btn1 = () => {
		const target = document.querySelector('#sideBar-blur') as HTMLElement;
		target.style.width = '350px';
	};

	const btn2 = () => {
		const target = document.querySelector('.sideBar-bar') as HTMLElement;
		target.style.width = '0';
		$(document).trigger('click');
	};

	const btn3 = () => {
		const target = document.querySelector('.sideBar-bar.sideBar-bar-photos') as HTMLElement;
		target.style.width = '350px';
	};

	const btn4 = () => {
		const target = document.querySelector('.sideBar-bar.sideBar-bar-colors') as HTMLElement;
		target.style.width = '350px';
	};

	/* Clicking Outside The Div Closing It */

	$(document).on('click', (e) => {
		if ($(e.target).closest('#sideBarBtn-blur').length === 0 && $(e.target).closest('#sideBar-blur').length === 0) {
			if ($(e.target).closest('#bottomBar-blur').length === 0 && $(e.target).closest('#bottomBar-blur2').length === 0) {
				if ($(e.target).closest('#List-blur').length === 0) {
					if ($(e.target).closest('#newList-blur').length === 0 && $(e.target).closest('#newList-blur1').length === 0) {
						const target = document.querySelector('.sideBar-bar') as HTMLElement;
						target.style.width = '0';
					}
				}
			}
		}
	});

	return (
		<>
			<ToggleBtn />
			<button
				title='Background'
				id='sideBarBtn-blur'
				onClick={btn1}
				className='sideBar-btn'>
				Background{' '}
				<svg
					xmlns='http://www.w3.org/2000/svg'
					width='2em'
					height='2em'
					viewBox='0 0 24 24'>
					<g
						fill='none'
						stroke='currentColor'
						strokeWidth='1.5'>
						<path d='M20.51 9.54a1.899 1.899 0 0 1-1 1.09A7 7 0 0 0 15.37 17c.001.47.048.939.14 1.4a2.16 2.16 0 0 1-.31 1.65a1.79 1.79 0 0 1-1.21.8a9 9 0 0 1-10.62-9.13A9.05 9.05 0 0 1 11.85 3h.51a9 9 0 0 1 8.06 5a2 2 0 0 1 .09 1.52z' />
						<path
							strokeLinecap='round'
							strokeLinejoin='round'
							d='m8 16.01l.01-.011M6 12.01l.01-.011M8 8.01l.01-.011M12 6.01l.01-.011M16 8.01l.01-.011'
						/>
					</g>
				</svg>
			</button>
			<aside
				id='sideBar-blur'
				className='sideBar-bar'>
				<header className='sideBar-head'>
					<h6 className='sideBar-header'>Background</h6>
					<span
						onClick={btn2}
						className='sideBar-close-btn'>
						
					</span>
				</header>
				<hr className='sideBar-hr' />
				<section className='sideBar-wrapper'>
					<section className='sideBar-wrap'>
						<div
							className='sideBar-photos-wrap'
							style={{ backgroundColor: btnImages }}>
							<LazyLoadImage
								src={Photos}
								onClick={btn3}
								className='sideBar-photos'
								alt='Photos'
								effect='blur'
							/>
						</div>
						<p className='sideBar-text'>Photos</p>
					</section>
					<section className='sideBar-wrap'>
						<div
							className='sideBar-colors-wrap'
							style={{ backgroundColor: btnColors }}>
							<LazyLoadImage
								src={Colors}
								onClick={btn4}
								className='sideBar-colors'
								alt='Colors'
								effect='blur'
							/>
						</div>
						<p className='sideBar-text'>Colors</p>
					</section>
				</section>
				<PhotosBar btn2={btn2} />
				<ColorsBar btn2={btn2} />
				<hr className='sideBar-hr' />
				<Buttons />
			</aside>
		</>
	);
}
