import './ScrollLeft.css';
import { useState, useEffect } from 'react';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';

export default function ScrollLeft() {
	const [offsetWidth, setOffsetWidth] = useState(0);
	const [scrollWidth, setScrollWidth] = useState(0);

	const listsNumber = document.querySelectorAll('.List');

	const toggleLeft = () => {
		const slider = document.querySelector('.Wrapper');
		const scrollLeft = document.querySelector('#angle-left') as HTMLButtonElement;

		//if (slider) {
		const scrolledLeft = slider?.scrollLeft;
		if (scrolledLeft! >= slider?.clientWidth! / 10) {
			scrollLeft.style.display = 'block';
		} else if (scrolledLeft! < slider?.clientWidth! / 10) {
			scrollLeft.style.display = 'none';
		}
		//}
	};

	const scrollLeft = () => {
		const slider = document.querySelector('.Wrapper');

		//if (slider) {
		/*slider?.scrollBy*/ slider?.scrollTo({
			left: slider?.scrollLeft - slider?.clientWidth /*-slider?.clientWidth*/,
			behavior: 'smooth',
		});
		//}
	};

	const resize = () => {
		const slider = document.querySelector('.Wrapper') as HTMLElement;

		/*console.log('width: ', slider.offsetWidth window.innerWidth + 'px');
		console.log('width: ', slider.scrollWidth + 'px');*/
		setOffsetWidth(slider.offsetWidth);
		setScrollWidth(slider.scrollWidth);
	};
	window.onresize = resize;

	/* Add The Event Listeners */

	useEffect(() => {
		const slider = document.querySelector('.Wrapper') as HTMLElement;

		setOffsetWidth(slider?.offsetWidth);
		setScrollWidth(slider?.scrollWidth!);

		if (offsetWidth < scrollWidth) {
			toggleLeft();
		} else if (offsetWidth >= scrollWidth) {
			toggleLeft();
		}
	}, [offsetWidth, scrollWidth, listsNumber]);

	useEffect(() => {
		const slider = document.querySelector('.Wrapper');

		//if (slider) {
		slider?.addEventListener('scroll', toggleLeft);
		return () => {
			slider?.removeEventListener('scroll', toggleLeft);
		};
		//}
	}, []);

	return (
		<button
			id='angle-left'
			onClick={scrollLeft}>
			<ChevronLeftIcon />
		</button>
	);
}
