import './BottomBar.css';
import { useState, useLayoutEffect, useRef, useContext } from 'react';
import $ from 'jquery';
import StoreApi from '../../Utils/StoreApi';
import { ContextTypes } from '../../@types/ContextTypes';

export default function BottomBar({ barTitle, barId }: { barTitle: string; barId: string }) {
	const [open, setOpen] = useState(false);
	const [newBar, setNewBar] = useState(barTitle);
	const { updateBar } = useContext(StoreApi) as ContextTypes;

	/* Keep Input Width Auto While Typing */

	const [width, setWidth] = useState(0);
	const span = useRef<HTMLHeadingElement>(null);

	useLayoutEffect(() => {
		if (span.current) {
			setWidth(span.current.offsetWidth);
		}
	}, [newBar]);

	/* Clicking Outside The Div Closing It */

	$(document).on('click', (e) => {
		if (open === true) {
			if ($(e.target).closest('#sideBarBtn-blur').length === 0 && $(e.target).closest('#sideBar-blur').length === 0) {
				if ($(e.target).closest('#bottomBar-blur').length === 0 && $(e.target).closest('#bottomBar-blur2').length === 0) {
					if ($(e.target).closest('#List-blur').length === 0) {
						if ($(e.target).closest('#newList-blur').length === 0 && $(e.target).closest('#newList-blur1').length === 0) {
							setOpen(!open);

							if (newBar.trim().length <= newBar.length) {
								setNewBar(newBar.trim());
							}
							newBar.trim().length === 0 ? setNewBar(barTitle) : updateBar(newBar.trim(), barId);
						}
					}
				}
			}
		}
	});

	/* On Key Down Events */

	const keyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
		if (e.key === 'Enter') {
			$(document).trigger('click');
		} else if (e.key === 'Escape') {
			$(document).trigger('click');
		}
	};

	return (
		<section className='bottomBar-toolbar'>
			<h5
				className='bottomBar-hide'
				ref={span}>
				{newBar}
			</h5>
			{open ? (
				<div id='bottomBar-blur'>
					<input
						style={{ width }}
						value={newBar}
						maxLength={512}
						onKeyDown={keyDown}
						onChange={(e) => setNewBar(e.target.value)}
						onFocus={(e) => e.target.select()}
						className='bottomBar-input'
						autoFocus
						name='text'
						type='text'
						autoComplete='off'
					/>
				</div>
			) : (
				<button
					title='Edit Bar'
					id='bottomBar-blur2'
					onClick={() => setOpen(!open)}
					className='bottomBar-wrapper'>
					{newBar}
				</button>
			)}
		</section>
	);
}
