import { useState, useEffect } from 'react';
import { MaxPics, PicsIncrement } from '../Components/Constants/Constants';
import { Debounce } from '../Utils/Debounce';

export const UseInfiniteScroll = () => {
	const [loading, setLoading] = useState(false);
	const [count, setCount] = useState(0 /*PicsIncrement*/);

	const handleClick = Debounce(
		() => {
			if (count !== 0 || loading) {
				return false;
			} else {
				setLoading(true);
			}
		},
		300,
		false,
	);

	const handleScroll = Debounce(
		() => {
			const element = document.querySelector('.sideBar-photos-colors-container');
			if (element) {
				if (element.clientHeight + element.scrollTop !== element.scrollHeight || loading) {
					return false;
				} else {
					setLoading(true);
				}
			}
		},
		1000,
		false,
	);

	useEffect(() => {
		if (!loading) {
			return;
		}

		if (count + PicsIncrement >= MaxPics) {
			setCount(MaxPics);
		} else {
			setCount(count + PicsIncrement);
		}
		setLoading(false);
	}, [count, loading]);

	useEffect(() => {
		const element2 = document.querySelector('.lazy-load-image-background');
		const element = document.querySelector('.sideBar-photos-colors-container');
		if (element && element2) {
			element2.addEventListener('click', handleClick);
			element.addEventListener('scroll', handleScroll);
			return () => {
				element2.removeEventListener('click', handleClick);
				element.removeEventListener('scroll', handleScroll);
			};
		}
	}, [handleClick, handleScroll]);

	return { count };
};
