import './Bars.css';
import BottomBar from './BottomBar';
import SideBars from '../SideBar/SideBars';

export default function Bars({ barTitle, barId }: { barTitle: string; barId: string }) {
	return (
		<>
			<header className='bars-toolbar'>
				<div className='bars-btn'>
					<div className='trello' />
				</div>
				<SideBars />
			</header>
			<BottomBar
				barTitle={barTitle}
				barId={barId}
			/>
		</>
	);
}
