import './Wrapper.css';
import { useState, useEffect } from 'react';
import { v4 as uuidv4 } from 'uuid';
import Lists from './Lists/Lists';
import Store from '../Utils/Store';
import StoreApi from '../Utils/StoreApi';
import { ContextTypes } from '../@types/ContextTypes';
import NewLists from './Lists/NewLists';
import ScrollRight from './ScrollBars/ScrollRight';
import ScrollLeft from './ScrollBars/ScrollLeft';
import ScrollToLeft from './ScrollBars/ScrollToLeft';
import ScrollToRight from './ScrollBars/ScrollToRight';
import { DragDropContext, Droppable, DropResult } from 'react-beautiful-dnd';

export default function Wrapper() {
	const [data, setData] = useState(Store);

	/* Update List Title */

	const updateListTitle = (title: string, listId: string) => {
		const list = data.lists[listId as keyof typeof data.lists];
		list.title = title;

		const newState = {
			...data,
			lists: {
				...data.lists,
				[listId]: list,
			},
		};
		setData(newState);
	};

	/* Add More Lists */

	const addMoreList = (title: string) => {
		if (title === '') {
			return;
		} else {
			const newListId = uuidv4();

			const newList = {
				id: newListId,
				title,
				cardIds: [],
			};
			const newState = {
				...data,
				lists: {
					...data.lists,
					[newListId]: newList,
				},
				listIds: [...data.listIds, newListId],
			};
			setData(newState);
		}
	};

	/* Delete Lists */

	const deleteList = (listId: string) => {
		const newLists = data.listIds;
		const newListIds = newLists.filter((list) => list !== listId);

		const newList = Object.keys(data.lists).reduce((acc, key) => {
			// Copy all except listId

			if (key !== listId) {
				acc[key as keyof typeof data.lists] = data.lists[key as keyof typeof data.lists];
			}
			return acc;
		}, {} as { [key in keyof typeof data.lists]: (typeof data.lists)[key] });

		const newState = {
			...data,
			lists: {
				...newList,
			},
			listIds: [...newListIds],
		};
		setData(newState);
	};

	/* Update Card Text */

	const updateCardText = (text: string, cardId: string) => {
		const card = data.cards[cardId as keyof typeof data.cards];
		card.text = text;

		const newState = {
			...data,
			cards: {
				...data.cards,
				[cardId]: card,
			},
		};
		setData(newState);
	};

	/* Add More Cards */

	const addMoreCard = (text: string, listId: string) => {
		if (text === '') {
			return;
		} else {
			const newCardId = uuidv4();

			const newCard = {
				id: newCardId,
				text,
			};

			const list = data.lists[listId as keyof typeof data.lists];
			/*list.cardIds = [...list.cardIds, newCardId];
			data.cards = {
				...data.cards,
				[newCardId]: newCard,
			};
			const newState = {
				...data,
			};*/

			const newState = {
				...data,
				cards: {
					...data.cards,
					[newCardId]: newCard,
				},
				lists: {
					...data.lists,
					[listId]: {
						...list,
						cardIds: [...list.cardIds, newCardId],
					},
				},
			};
			setData(newState);
		}
	};

	/* Delete Cards */

	const deleteCard = (cardId: string, listId: string) => {
		const list = data.lists[listId as keyof typeof data.lists];
		const newCards = list.cardIds;
		const newCardIds = newCards.filter((card: string) => card !== cardId);

		const newCard = Object.keys(data.cards).reduce((acc, key) => {
			// Copy all except cardId

			if (key !== cardId) {
				acc[key as keyof typeof data.cards] = data.cards[key as keyof typeof data.cards];
			}
			return acc;
		}, {} as { [key in keyof typeof data.cards]: (typeof data.cards)[key] });

		const newState = {
			...data,
			cards: {
				...newCard,
			},
			lists: {
				...data.lists,
				[listId]: {
					...list,
					cardIds: [...newCardIds],
				},
			},
		};

		setData(newState);
	};

	/* Drag Lists & Cards */

	const onDragEnd = (result: DropResult) => {
		const { destination, source, draggableId, type } = result;

		if (!result.destination) {
			return;
		}

		/* Drag Cards */

		if (type === 'card' && destination) {
			const sourceList = data.lists[source.droppableId as keyof typeof data.lists];
			const destinationList = data.lists[destination.droppableId as keyof typeof data.lists];

			sourceList.cardIds.splice(source.index, 1);
			destinationList.cardIds.splice(destination.index, 0, draggableId);

			const newState = {
				...data,
				lists: {
					...data.lists,
					[sourceList.id]: sourceList,
					[destinationList.id]: destinationList,
				},
			};
			setData(newState);
		}

		/* Drag Lists */

		if (type === 'list' && destination) {
			const newListIds = data.listIds;
			newListIds.splice(source.index, 1);
			newListIds.splice(destination.index, 0, draggableId);
			const newState = {
				...data,
				listIds: newListIds,
			};
			setData(newState);
		}
	};

	/* Move The Scroll By Clicking The Entire Page */

	useEffect(() => {
		const slider = document.querySelector('.Wrapper') as HTMLElement;
		let mouseDown = false;
		let startX: number, scrollLeft: number;

		const startDragging = (e: MouseEvent) => {
			/* Prevent Children Effect Except All .Lists */

			const target = e.target as HTMLElement;
			if (e.currentTarget /*(.Wrapper)*/ === e.target || target?.classList.contains('Lists')) {
				mouseDown = true;
				if (slider) {
					startX = e.pageX - slider.offsetLeft;
					scrollLeft = slider.scrollLeft;
				}
			}
		};
		const stopDragging = () => {
			mouseDown = false;
		};

		const moveDragging = (e: MouseEvent) => {
			if (!mouseDown) {
				return;
			}
			const x = e.pageX - slider.offsetLeft;
			const scroll = (x - startX) * 1; //scrollSpeed
			slider.scrollLeft = scrollLeft - scroll;
		};

		/* Add The Event Listeners */

		slider.addEventListener('mousemove', moveDragging);
		slider.addEventListener('mousedown', startDragging);
		slider.addEventListener('mouseup', stopDragging);
		slider.addEventListener('mouseleave', stopDragging);
		return () => {
			slider.removeEventListener('mousemove', moveDragging);
			slider.removeEventListener('mousedown', startDragging);
			slider.removeEventListener('mouseup', stopDragging);
			slider.removeEventListener('mouseleave', stopDragging);
		};
	}, []);

	const value = {
		updateListTitle,
		addMoreList,
		deleteList,
		updateCardText,
		addMoreCard,
		deleteCard,
	};

	return (
		<StoreApi.Provider value={value as ContextTypes}>
			<DragDropContext onDragEnd={onDragEnd}>
				<Droppable
					droppableId='list'
					direction='horizontal'
					type='list'>
					{(provided) => (
						<section
							className='Wrapper'
							ref={provided.innerRef}
							{...provided.droppableProps}>
							{data.listIds.map((listId, index) => {
								const list = data.lists[listId as keyof typeof data.lists];
								const cards = list.cardIds.map((cardId: string) => data.cards[cardId as keyof typeof data.cards]);
								return (
									<Lists
										list={list}
										listId={list.id}
										key={list.id}
										cards={cards}
										index={index}
									/>
								);
							})}
							{provided.placeholder}
							<NewLists />
						</section>
					)}
				</Droppable>
			</DragDropContext>
			<section>
				<ScrollRight />
				<ScrollToLeft />
				<ScrollToRight />
				<ScrollLeft />
			</section>
		</StoreApi.Provider>
	);
}
