import './NewLists.css';
import { useState, useRef, useContext } from 'react';
import $ from 'jquery';
import AddIcon from '@mui/icons-material/Add';
import ClearIcon from '@mui/icons-material/Clear';
import StoreApi from '../../Utils/StoreApi';
import { ContextTypes } from '../../@types/ContextTypes';

export default function NewLists() {
	const [open, setOpen] = useState(false);
	const [text, setText] = useState('');
	const { addMoreList } = useContext(StoreApi) as ContextTypes;

	const inputRef = useRef<HTMLInputElement>(null);

	/* On Clicking Buttons Events */

	const btn1 = () => {
		if (inputRef.current && text.trim().length === 0) {
			inputRef.current.focus();
			setText('');
			setOpen(open);
		} else {
			addMoreList(text.trim());
			setText('');
			setOpen(!open);
		}
	};

	const btn2 = () => {
		setOpen(!open);
		setText('');
	};

	const focusInput = (e: React.FocusEvent<HTMLInputElement, Element>) => {
		const slider = document.querySelector('.Wrapper');

		const listLeft = e.target /*e.currentTarget*/
			.getBoundingClientRect().left;

		/*Scroll Title Into Right View*/

		if (slider && slider.clientWidth - listLeft < 276) {
			/*slider.scrollBy*/ slider.scrollTo({
				left: slider.scrollWidth,
				behavior: 'smooth',
			});
		}
	};

	const btn3 = () => {
		setOpen(!open);

		/*Prevent React Beautiful DND Default Scroll*/
		/*e.target.scrollIntoView();

		//focusInput();

		const slider = document.querySelector('.Wrapper');

		const listLeft = e.target /*e.currentTarget*/
		/*.getBoundingClientRect().left;

		/*Scroll Title Into Right View*/

		/*if (slider.clientWidth - listLeft < 276) {
			/*slider.scrollBy*/
		/*slider.scrollTo({
				left: slider.scrollWidth,
				behavior: 'smooth',
			});
		}*/
	};

	/* Clicking Outside The Div Closing It */

	$(document).on('click', (e) => {
		if (open === true) {
			if ($(e.target).closest('#bottomBar-blur').length === 0 && $(e.target).closest('#bottomBar-blur2').length === 0) {
				if ($(e.target).closest('#List-blur').length === 0) {
					if ($(e.target).closest('#newList-blur').length === 0 && $(e.target).closest('#newList-blur1').length === 0) {
						setOpen(!open);
						setText('');
					}
				}
			}
		}
	});

	/* On Key Down Events */

	const keyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
		if (e.key === 'Enter') {
			$(document).trigger('click');
			btn1();
		} else if (e.key === 'Escape') {
			$(document).trigger('click');
			setText(text.trim());
			if (text.trim().length === 0) {
				setText('');
			}
		}
	};

	return (
		<>
			{open ? (
				<div
					className='newList-input-wrapper'
					id='newList-blur'>
					<div className='newList-input-wrap'>
						<input
							autoFocus
							ref={inputRef}
							onKeyDown={keyDown}
							onChange={(e) => setText(e.target.value)}
							onFocus={focusInput}
							placeholder='Enter list title...'
							className='newList-input'
							maxLength={512}
							name='text'
							type='text'
							autoComplete='off'
							value={text}
						/>
					</div>
					<div>
						<button
							title='Add list'
							className='newList-btn'
							onClick={btn1}>
							Add list
						</button>
						<ClearIcon
							className='newList-icon'
							onClick={btn2}
						/>
					</div>
				</div>
			) : (
				<section
					id='newList-blur1'
					className='newList-wrapper'
					onClick={btn3}>
					<AddIcon />
					<p>Add another list</p>
				</section>
			)}
		</>
	);
}
