import './ColorsBar.css';
import { useContext } from 'react';
import $ from 'jquery';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import Colors from '../../Utils/Colors';
import LinearGradients from '../../Utils/LinearGradients';
import StoreApi from '../../Utils/StoreApi';
import { ContextTypes } from '../../@types/ContextTypes';

export default function ColorsBar({ btn2 }: { btn2: () => void }) {
	const { setBgColor, setBgImage } = useContext(StoreApi) as ContextTypes;

	const btn1 = () => {
		const target = document.querySelector('.sideBar-bar.sideBar-bar-colors') as HTMLElement;
		target.style.width = '0';
	};

	return (
		<section className='sideBar-bar sideBar-bar-colors'>
			<header className='sideBar-head-colors'>
				<ChevronLeftIcon
					id='fa-angle-left'
					onClick={btn1}
				/>
				<h6 className='sideBar-header'>Colors</h6>
				<span
					onClick={btn2}
					className='sideBar-close-btn-colors'>
					
				</span>
			</header>
			<hr className='sideBar-hr' />
			<section className='sideBar-photos-colors-container overflow'>
				{LinearGradients.map((LinearGradient, index) => {
					const btnLinearGradient = (e: { stopPropagation: () => void }) => {
						e.stopPropagation();
						setBgImage(LinearGradient);
						setBgColor('');

						const html = `<svg
		role='presentation'
		focusable='false'
		viewBox='0 0 24 24'
		xmlns='http://www.w3.org/2000/svg'
		class='svg-wrap'>
		<path
		d='M6.73534 12.3223C6.36105 11.9162 5.72841 11.8904 5.3223 12.2647C4.91619 12.639 4.89039 13.2716 5.26467 13.6777L8.87678 17.597C9.41431 18.1231 10.2145 18.1231 10.7111 17.6264C10.7724 17.5662 10.7724 17.5662 11.0754 17.2683C11.3699 16.9785 11.6981 16.6556 12.0516 16.3075C13.0614 15.313 14.0713 14.3169 15.014 13.3848L15.0543 13.3449C16.7291 11.6887 18.0004 10.4236 18.712 9.70223C19.0998 9.30904 19.0954 8.67589 18.7022 8.28805C18.309 7.90022 17.6759 7.90457 17.2881 8.29777C16.5843 9.01131 15.3169 10.2724 13.648 11.9228L13.6077 11.9626C12.6662 12.8937 11.6572 13.8889 10.6483 14.8825C10.3578 15.1685 10.0845 15.4375 9.83288 15.6851L6.73534 12.3223Z'
		fill='currentColor'
		></path>
		</svg><svg
		role='presentation'
		focusable='false'
		viewBox='0 0 24 24'
		xmlns='http://www.w3.org/2000/svg'
		class='svg'>
		<path
		d='M6.73534 12.3223C6.36105 11.9162 5.72841 11.8904 5.3223 12.2647C4.91619 12.639 4.89039 13.2716 5.26467 13.6777L8.87678 17.597C9.41431 18.1231 10.2145 18.1231 10.7111 17.6264C10.7724 17.5662 10.7724 17.5662 11.0754 17.2683C11.3699 16.9785 11.6981 16.6556 12.0516 16.3075C13.0614 15.313 14.0713 14.3169 15.014 13.3848L15.0543 13.3449C16.7291 11.6887 18.0004 10.4236 18.712 9.70223C19.0998 9.30904 19.0954 8.67589 18.7022 8.28805C18.309 7.90022 17.6759 7.90457 17.2881 8.29777C16.5843 9.01131 15.3169 10.2724 13.648 11.9228L13.6077 11.9626C12.6662 12.8937 11.6572 13.8889 10.6483 14.8825C10.3578 15.1685 10.0845 15.4375 9.83288 15.6851L6.73534 12.3223Z'
		fill='currentColor'
		></path>
		</svg>`;

						const el = document.querySelectorAll('.sideBar-colors-picker *:nth-child(1n+1)');

						for (let i = 0; i < el.length; i++) {
							el[i].remove();
						}

						localStorage.setItem('storedSvg', JSON.stringify(index) /*(document.querySelector('.sideBar-colors-wrap').value = i)*/);

						$('.sideBar-colors-picker')[index].insertAdjacentHTML('afterbegin', html);

						localStorage.removeItem('storedInterval');
						localStorage.removeItem('storedBgPhotos');
						localStorage.removeItem('storedBgNextBackground');
						localStorage.removeItem('storedBgOneSecond');
						localStorage.removeItem('storedBgThirtySecond');
						localStorage.removeItem('storedBgOneMinute');
						localStorage.removeItem('storedBgFiveMinutes');
						localStorage.removeItem('storedBgTenMinutes');

						const target1 = document.querySelector('.sideBar-photos-wrap') as HTMLDivElement;
						const target2 = document.querySelector('.next-background') as HTMLButtonElement;
						const target3 = document.querySelector('.page-load') as HTMLButtonElement;
						const target4 = document.querySelector('.one-second') as HTMLButtonElement;
						const target5 = document.querySelector('.thirty-second') as HTMLButtonElement;
						const target6 = document.querySelector('.one-minute') as HTMLButtonElement;
						const target7 = document.querySelector('.five-minutes') as HTMLButtonElement;
						const target8 = document.querySelector('.ten-minutes') as HTMLButtonElement;
						if (target1 && target2 && target3 && target4 && target5 && target6 && target7 && target8) {
							target1.style.backgroundColor =
								target2.style.backgroundColor =
								target3.style.backgroundColor =
								target4.style.backgroundColor =
								target5.style.backgroundColor =
								target6.style.backgroundColor =
								target7.style.backgroundColor =
								target8.style.backgroundColor =
									'';
						}

						localStorage.setItem(
							'storedBgColors',
							JSON.stringify(LinearGradient) /*(document.querySelector('.Index').style.backgroundImage = LinearGradient)*/,
						);
					};

					return (
						<div
							className='sideBar-colors-picker'
							onClick={btnLinearGradient}
							key={index}
							style={{ backgroundImage: LinearGradient }}
						/>
					);
				})}
				<hr className='sideBar-hr' />
				{Colors.map((color, index) => {
					const btnColor = (e: { stopPropagation: () => void }) => {
						e.stopPropagation();
						setBgColor(color);
						setBgImage('');

						const html = `<svg
		role='presentation'
		focusable='false'
		viewBox='0 0 24 24'
		xmlns='http://www.w3.org/2000/svg'
		class='svg-wrap'>
		<path
		d='M6.73534 12.3223C6.36105 11.9162 5.72841 11.8904 5.3223 12.2647C4.91619 12.639 4.89039 13.2716 5.26467 13.6777L8.87678 17.597C9.41431 18.1231 10.2145 18.1231 10.7111 17.6264C10.7724 17.5662 10.7724 17.5662 11.0754 17.2683C11.3699 16.9785 11.6981 16.6556 12.0516 16.3075C13.0614 15.313 14.0713 14.3169 15.014 13.3848L15.0543 13.3449C16.7291 11.6887 18.0004 10.4236 18.712 9.70223C19.0998 9.30904 19.0954 8.67589 18.7022 8.28805C18.309 7.90022 17.6759 7.90457 17.2881 8.29777C16.5843 9.01131 15.3169 10.2724 13.648 11.9228L13.6077 11.9626C12.6662 12.8937 11.6572 13.8889 10.6483 14.8825C10.3578 15.1685 10.0845 15.4375 9.83288 15.6851L6.73534 12.3223Z'
		fill='currentColor'
		></path>
		</svg><svg
		role='presentation'
		focusable='false'
		viewBox='0 0 24 24'
		xmlns='http://www.w3.org/2000/svg'
		class='svg'>
		<path
		d='M6.73534 12.3223C6.36105 11.9162 5.72841 11.8904 5.3223 12.2647C4.91619 12.639 4.89039 13.2716 5.26467 13.6777L8.87678 17.597C9.41431 18.1231 10.2145 18.1231 10.7111 17.6264C10.7724 17.5662 10.7724 17.5662 11.0754 17.2683C11.3699 16.9785 11.6981 16.6556 12.0516 16.3075C13.0614 15.313 14.0713 14.3169 15.014 13.3848L15.0543 13.3449C16.7291 11.6887 18.0004 10.4236 18.712 9.70223C19.0998 9.30904 19.0954 8.67589 18.7022 8.28805C18.309 7.90022 17.6759 7.90457 17.2881 8.29777C16.5843 9.01131 15.3169 10.2724 13.648 11.9228L13.6077 11.9626C12.6662 12.8937 11.6572 13.8889 10.6483 14.8825C10.3578 15.1685 10.0845 15.4375 9.83288 15.6851L6.73534 12.3223Z'
		fill='currentColor'
		></path>
		</svg>`;

						const el = document.querySelectorAll('.sideBar-colors-picker *:nth-child(1n+1)');

						for (let i = 0; i < el.length; i++) {
							el[i].remove();
						}

						localStorage.setItem('storedSvg', JSON.stringify(index + 79) /*(document.querySelector('.sideBar-colors-wrap').value = i)*/);

						$('.sideBar-colors-picker')[index + 79].insertAdjacentHTML('afterbegin', html);

						localStorage.removeItem('storedInterval');
						localStorage.removeItem('storedBgPhotos');
						localStorage.removeItem('storedBgNextBackground');
						localStorage.removeItem('storedBgOneSecond');
						localStorage.removeItem('storedBgThirtySecond');
						localStorage.removeItem('storedBgOneMinute');
						localStorage.removeItem('storedBgFiveMinutes');
						localStorage.removeItem('storedBgTenMinutes');

						const target1 = document.querySelector('.sideBar-photos-wrap') as HTMLDivElement;
						const target2 = document.querySelector('.next-background') as HTMLButtonElement;
						const target3 = document.querySelector('.page-load') as HTMLButtonElement;
						const target4 = document.querySelector('.one-second') as HTMLButtonElement;
						const target5 = document.querySelector('.thirty-second') as HTMLButtonElement;
						const target6 = document.querySelector('.one-minute') as HTMLButtonElement;
						const target7 = document.querySelector('.five-minutes') as HTMLButtonElement;
						const target8 = document.querySelector('.ten-minutes') as HTMLButtonElement;
						if (target1 && target2 && target3 && target4 && target5 && target6 && target7 && target8) {
							target1.style.backgroundColor =
								target2.style.backgroundColor =
								target3.style.backgroundColor =
								target4.style.backgroundColor =
								target5.style.backgroundColor =
								target6.style.backgroundColor =
								target7.style.backgroundColor =
								target8.style.backgroundColor =
									'';
						}

						localStorage.setItem('storedBgColors', JSON.stringify(color) /*(document.querySelector('.Index').style.backgroundColor = color)*/);
					};

					return (
						<div
							className='sideBar-colors-picker'
							onClick={btnColor}
							key={index}
							style={{ backgroundColor: color }}
						/>
					);
				})}
			</section>
		</section>
	);
}
